<template>
  <div class='hundred'>
    <div style='height:1px'></div>
    <div>
        <div class='bankgroud noPadding'>
            <div class='allFlex planBack'>
                <div>
                    <img :src="banks[cardList.BANK_NAME]?require(`../../assets/bank/white/${banks[cardList.BANK_NAME]}.png`):require('../../assets/bank/white/yl.png')" class='bankLogo' />
                    <span class='bankRight'>
                        {{cardList.short_cn_name}}
                    </span>
                </div>
                <div class='textRight'>
                    <div>
                      <span class='repayPadding garyChoose whiteColor repayFont'>尾号{{cardList.BANK_ACCOUNT.substring(cardList.BANK_ACCOUNT.length-4, cardList.BANK_ACCOUNT.length)}}</span>
                    </div>
                </div>
            </div>
            <div class='allFlex choose border planBackx'>
                <div class='garyChoose smallFont half textCenter'>
                    <p class='whiteColor'>{{cardList.BILL_DAY}}</p>
                    <p>账单日</p>
                </div>
                <div class='garyChoose smallFont half textCenter leftRight'>
                    <p class='whiteColor'>{{cardList.REPAYMENT_DAY}}</p>
                    <p>还款日</p>
                </div>
                <div class='garyChoose smallFont half textCenter'>
                    <p class='whiteColor'>{{cardList.LIMIT_MONEY}}</p>
                    <p>额度</p>
                </div>
            </div>
            <div class='leftRaduis'></div>
            <div class='rightRaduis'></div>
        </div>
        <div class='planContent'>
          <ul>
            <li class='allFlex justifyBetween flexPadding'>
              <div class='gary'>还款全额</div>
              <div>{{money}}</div>
            </li>
            <li class='allFlex justifyBetween flexPadding'>
              <div class='gary'>还款周期</div>
              <div>{{searchTime}} - {{endTime}}</div>
            </li>
            <li class='allFlex justifyBetween flexPadding' v-if="tong != 'QYK'">
              <div class='gary'>日还款笔数</div>
              <div>{{value}}</div>
            </li>
            <li class='allFlex justifyBetween flexPadding' v-if="tong != 'QYK'">
              <div class='gary'>周转金</div>
              <div>{{calcList[40]}}</div>
            </li>
            <li class='allFlex justifyBetween flexPadding'>
              <div class='gary'>还款手续费</div>
              <div>{{calcList[9]}}</div>
            </li>
            <li class='allFlex justifyBetween flexPadding'>
              <div class='gary'>还款笔数费</div>
              <div>{{calcList[7]}}</div>
            </li>
            <li class='allFlex justifyBetween flexPadding'>
              <div class='gary'>{{tong != 'QYK' ? '周转金总额' : '手续费总额'}}</div>
              <div>{{tong != 'QYK' ? zhou : shou}}</div>
            </li>
            <li class='allFlex justifyBetween flexPadding'>
              <div class='gary'>消费地区</div>
              <div>{{area}}</div>
            </li>
          </ul>
          <div class='allFlex justifyBetween planMargin'>
            <router-link :to="{name: 'preview', query: { acqcode: $route.query.acqcode, rate: $route.query.rate, children: $route.query.children, keep: 1, planItem: planItem, money: money, searchTime: searchTime, endTime: endTime, calcList: JSON.stringify(calcList), cardList: JSON.stringify(cardList), value: value, tong: tong, merchantNo: usermerchantNo }}" class='submitPlan'>预览详细计划</router-link>
            <router-link :to="{name: 'make', query: { acqcode: $route.query.acqcode, rate: $route.query.rate, keep: 1, item: JSON.stringify(cardList), tong: tong}}" tag='div' class='submitPlan'>修改计划</router-link>
          </div>
          <div class='loginTo makeTo'>
            <div class='blue' @click="tong != 'QYK'?next():nextSmall()"  element-loading-background="rgba(0, 0, 0, 0.7)" v-loading.fullscreen.lock='fullscreenLoading'>
              提交计划
            </div>
          </div>
      </div>
    </div>
    <div class='bottomLong'></div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      version: '',
      agentNo: '',
      merchantNo: '',
      usermerchantNo: '',
      cardList: [],
      banks: {
        313003: 'bj',
        303: 'gd',
        306: 'gf',
        105: 'js',
        301: 'jt',
        305: 'ms',
        103: 'ny',
        307: 'pa',
        309: 'xy',
        310: 'pf',
        403: 'yz',
        308: 'zs',
        102: 'gs',
        104: 'zg',
        302: 'zx',
        313062: 'sh',
        304: 'hx'
      },
      now: -1,
      calcList: [],
      searchTime: '',
      endTime: '',
      value: 0,
      valuex: 0,
      money: 0,
      planItem: '',
      fullscreenLoading: false,
      area: '',
      zhou: 0,
      shou: 0
    }
  },
  created () {
    this.version = this.$stact.state.version
    this.agentNo = this.$stact.state.agentNo
    this.merchantNo = JSON.parse(this.$stact.state.token)[0].merchantNo
    this.searchTime = this.$route.query.searchTime
    this.endTime = this.$route.query.endTime
    this.calcList = JSON.parse(this.$route.query.calcList)
    this.zhou = Math.floor(Number(this.calcList[40]) * 100 + Number(this.calcList[9]) * 100 + Number(this.calcList[7]) * 100) / 100
    this.shou = Math.floor(Number(this.calcList[9]) * 100 + Number(this.calcList[7]) * 100) / 100
    this.value = this.$route.query.value
    this.valuex = this.$route.query.valuex
    this.money = this.$route.query.money
    this.planItem = this.$route.query.planItem
    this.cardList = JSON.parse(this.$route.query.cardList)
    this.area = this.$route.query.area
    this.tong = this.$route.query.tong
    this.usermerchantNo = this.$route.query.merchantNo
  },
  methods: {
    next () {
      let vm = this
      if (vm.usermerchantNo) {
        vm.merchantNo = vm.usermerchantNo
      }
      let parmas = {
        '0': '0700',
        '3': '190210',
        '8': vm.money,
        '9': vm.zhou,
        '10': Date.parse(vm.searchTime.replace(/-/g, '/')),
        '11': Date.parse(vm.endTime.replace(/-/g, '/')),
        '12': vm.calcList[9],
        '13': vm.calcList[7],
        '14': vm.$route.query.value,
        '16': '2',
        '42': vm.merchantNo,
        '43': vm.$route.query.acqcode,
        '57': vm.$route.query.planItem,
        '59': vm.version
      }
      let url = vm.$mdata.mdGet(parmas)
      vm.fullscreenLoading = true
      vm.$http.post('request.app', url)
        .then(res => {
          vm.fullscreenLoading = false
          if (res.data[39] === '00') {
            vm.$router.push({ name: 'repayment' })
          } else {
            vm.$message({
              message: res.data[39],
              center: true,
              offset: 30,
              duration: 2500,
              type: 'success'
            })
          }
        })
        .catch(err => {
          vm.fullscreenLoading = false
          console.log(err)
        })
    },
    nextSmall () {
      let vm = this
      document.querySelectorAll('.indexDom').forEach(function (item, index) {
        if (document.querySelectorAll('.indexDom .el-input__inner')[index].value) {
          vm.planItem[item.dataset.myindex].customizecity = document.querySelectorAll('.indexDom .el-input__inner')[index].value.replace(/\s*/g, '')
        }
        if (document.querySelectorAll('.companyDom .el-input__inner')[index].value) {
          vm.planItem[item.dataset.myindex].cityindustryName = document.querySelectorAll('.companyDom .el-input__inner')[index].value
          vm.planItem[item.dataset.myindex].cityindustry = vm.company[item.dataset.myindex].value
        }
      })
      if (vm.usermerchantNo) {
        vm.merchantNo = vm.usermerchantNo
      }
      let parmas = {
        '0': '0700',
        '3': '390049',
        '8': vm.money,
        '10': vm.searchTime + ' 00:00:00',
        '11': vm.endTime + ' 23:59:59',
        '12': vm.calcList[9],
        '13': vm.calcList[7],
        '42': vm.merchantNo,
        '43': vm.$route.query.acqcode,
        '57': vm.$route.query.planItem,
        '59': vm.version
      }
      let url = vm.$mdata.mdGet(parmas)
      vm.fullscreenLoading = true
      vm.$http.post('request.app', url)
        .then(res => {
          vm.fullscreenLoading = false
          if (res.data[39] === '00') {
            vm.$router.push({ name: 'repayment' })
          } else {
            vm.$message({
              message: res.data.msg,
              center: true,
              offset: 30,
              duration: 2500,
              type: 'success'
            })
          }
        })
        .catch(err => {
          vm.fullscreenLoading = false
          console.log(err)
        })
    }
  }
}
</script>
